<template>
    <div class="user_exit" v-if="this.define===false">
        <h2>사용자명 : {{this.user.username}}</h2>
        <h4>e-mail : {{this.user.email}}</h4>
    </div>
    <div class="user_exit" v-if="this.define===true">
        <h2>o</h2>
        <h4>o</h4>
    </div>
    <b-container>
        <b-row>
            <b-col></b-col>
            <b-col><b-button class="btn" variant="primary" to="/home" size="sm" v-if="this.define===false">홈</b-button></b-col>
            <b-col><b-button class="btn" variant="danger" @click="qdefine" size="sm" v-if="this.define===false">회원탈퇴</b-button></b-col>
            <b-col></b-col>
        </b-row>
    </b-container>    
    <div class="d-flex justify-content-center v-if=this.define===true">
        <b-button class="btn" variant="primary" @click="onCancle" size="lg" v-if="this.define===true">아니오</b-button>
        <b-button class="btn" variant="danger" @click="onDeleteUser(this.user)" size="lg" v-if="this.define===true">탈퇴 하겠습니까</b-button>
    </div>

</template>

<script>
import {ref} from 'vue';
const define = ref(false);
import router from "@/router";

export default{
    name: "UserInfo",
    data() {
        return{
            user:'',
            define: define,
        }        
    },    
    mounted() {
        this.user = this.$store.state.auth.user;
    },
    methods: {
        qdefine() {
            this.define = this.define ?  false: true;
        },
        onDeleteUser(user) {
            this.$store.dispatch("fireBaseDeleteUser", user);             
            this.$store.dispatch("signOut");        
        },
        onCancle() {
            this.define=false;
            router.push("/home");
        }        
    },
}

</script>

<style scoped>
.user_exit {
    padding: 40px;
    text-align: center;
}
</style>
